import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import {saveAs as importedSaveAs} from "file-saver";
import { FormControl } from '@angular/forms';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';


@Component({
  selector: 'app-adminpage',
  templateUrl: './adminpage.component.html',
  styleUrls: ['./adminpage.component.css']
})
export class AdminpageComponent implements OnInit {

  year: FormControl = new FormControl("");
  type: FormControl = new FormControl("");
  user;

  constructor(private httpC : HttpClient, private router: Router) { }
  msg = false;
  poreskiHide = false;
  msgText = "Molimo Vas unesite sve parametre";
  ngOnInit(): void {
      if(localStorage["user"] != 'olivera' && localStorage["user"] != 'abijelic') {
        this.router.navigate(['unauthorized']);
        return;
      }
      this.httpC.get(environment.authURL + 'getLogged').subscribe(data => {
        //logged
        this.user = data;
        if(this.user.email != 'olivera' && this.user.email != 'abijelic') {
          this.router.navigate(['unauthorized']);
          return;
        }
      }, error => {
        console.log(error.error.message);
        this.router.navigate(['unauthorized']);
        return;
      })
  }

  download() {
    if(this.type.value == "" || this.year.value == "") {
      this.msg = true;
      return;
    }
    this.msg = false;
    if(this.type.value == "PDV") {
      this.generateFile().subscribe(data => {
        var blob = new Blob([data], { type: 'blob' });
        const a = document.createElement('a');
        document.body.appendChild(a);
         
         const url = window.URL.createObjectURL(blob);
         a.href = url;
         a.download = "Izvestaj-" + this.year.value + ".xls";
         a.click();
         window.URL.revokeObjectURL(url);
     });
    } else {
      this.generateFileDrugo().subscribe(data => {
        var blob = new Blob([data], { type: 'blob' });
        const a = document.createElement('a');
        document.body.appendChild(a);
         
         const url = window.URL.createObjectURL(blob);
         a.href = url;
         a.download = "Izvestaj-" + this.year.value + ".xls";
         a.click();
         window.URL.revokeObjectURL(url);
     });
    }
      

  }

  onChangeGodina(value) {
  
    if(value != '2019') {
      this.poreskiHide = true;
    } else {
      this.poreskiHide = false
    }

}

  generateFile() : Observable<any> {
    return this.httpC.get(environment.pdvURL + "generateExcel/" + this.year.value,  { responseType: 'blob'});
  }

  generateFileDrugo() : Observable<any> {
    return this.httpC.get(environment.pdvURL + "generateExcelTroskovi/" + this.year.value,  { responseType: 'blob'});
  }

}
